<template>
    <div id="app" class="container-fluid mb-10">
      <div class="list-group fixed-top" v-show="basicGroups">
        <a href="#" @click="toggleOptionalStock(0)" class="list-group-item list-group-item-action list-group-item-primary">自選股１</a>
        <a href="#" @click="toggleOptionalStock(1)" class="list-group-item list-group-item-action list-group-item-secondary">自選股２</a>
        <a href="#" @click="toggleOptionalStock(2)" class="list-group-item list-group-item-action list-group-item-primary">自選股３</a>
        <a href="#" @click="toggleOptionalStock(3)" class="list-group-item list-group-item-action list-group-item-secondary">自選股４</a>
        <a href="#" @click="toggleOptionalStock(4)" class="list-group-item list-group-item-action list-group-item-primary">自選股５</a>
      </div>
        <div v-show="basicGroups">
          
          
        </div>
          
        <vue-bootstrap4-table :classes="classes"
                              :rows="rows"
                              :columns="columns"
                              :config="config"
                              :custom-filters="customFilters"
                              @on-select-row="onSelectRows"
                              @refresh-data="onRefreshData"
                              :show-loader="showLoader"
                              >
          <!-- 標題 -->
          <template slot="card-header">
              <div class="text-center">
                  <img src="/img/icons/favicon-32x32.png"> 財穀資訊網-選股
              </div>
          </template>

          <!-- 全域搜尋 (Global search) -->
          <template slot="global-search-clear-icon">
              <i class="fas fa-times-circle"></i>
          </template>
          <template slot="simple-filter-clear-icon">
              <i class="fas fa-times-circle"></i>
          </template>

          <template slot="refresh-button-text">
              <font-awesome-icon icon="user-secret" /> {{optionalStockText}}
          </template>
          <template slot="reset-button-text">
              ⟳ 重新設定
          </template>

          <!-- 排序 -->
          <template slot="sort-asc-icon">
              <font-awesome-icon icon="sort-up" style="color:blue" />
          </template>
          <template slot="sort-desc-icon">
              <font-awesome-icon icon="sort-down" style="color:blue" />
          </template>
          <template slot="no-sort-icon">
              <font-awesome-icon icon="sort"/>
          </template>

          <!-- 分頁 -->
          <template slot="paginataion-previous-button">
              <i class="fas fa-step-backward"></i>
          </template>
          <template slot="paginataion-next-button">
              <i class="fas fa-step-forward"></i>
          </template>
          <!-- 分頁說明 -->
          <template slot="pagination-info" slot-scope="props">
            此頁筆數：{{props.currentPageRowsLength}} |
            篩選筆數：{{props.filteredRowsLength}} | 
            總筆數：{{props.originalRowsLength}}
          </template>

          <template slot="empty-results">
              並無篩選出股票
          </template>

          <!-- 自定義按鈕 -->
          
          <template v-if="show_actionBtn" slot="vbt-action-buttons">
              <div class="dropdown">
                <button class="btn btn-primary dropdown-toggle" type="button" id="dropdownMenu2" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  切換面向
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenu2">
                  <template>
                      <div>
                          <bInfo @subLoadRows="subLoadBInfo" @subSelectColumns="subSelect" v-bind:teamList="teamList"></bInfo>
                      </div>
                  </template>
                  <template>
                      <div>
                          <bEps @subLoadRows="subLoadBEps" @subSelectColumns="subSelect" v-bind:teamList="teamList"></bEps>
                      </div>
                  </template>
                </div>
              </div>
          </template>

          
          

          <!-- Column Color Start 顏色定義 -->
          <!-- 公司基本資料 -->
          <template slot="b_info_code" slot-scope="props">
            <!-- <a target="_blank" v-bind:href="'https://goodinfo.tw/StockInfo/StockDetail.asp?STOCK_ID=' + props.cell_value" >{{props.cell_value}}</a> -->
            <a target="_blank" v-bind:href="'https://www.cmoney.tw/follow/channel/stock-' + props.cell_value" >{{props.cell_value}}</a>
            
            <img v-bind:src="'https://fsv.cmoney.tw/cmstatic/t/images/stock/dayline/' + props.cell_value + '.png'" style="border:0px;width:28px;" alt="">
          </template>

          <!-- 獲利能力_歷年加權平均 -->
          <template slot="b_eps_code" slot-scope="props">
            <a target="_blank" v-bind:href="'https://www.cmoney.tw/follow/channel/stock-' + props.cell_value" >{{props.cell_value}}</a>
            
            <img v-bind:src="'https://fsv.cmoney.tw/cmstatic/t/images/stock/dayline/' + props.cell_value + '.png'" style="border:0px;width:28px;" alt="">
          </template>
          <!-- Column Color End 顏色定義 -->
        </vue-bootstrap4-table>
        
    </div>
</template>

<script>
    import bInfo from './bInfo.vue';
    import bEps from './bEps.vue';
    import VueBootstrap4Table from 'vue-bootstrap4-table';
    import axios from 'axios';

    export default {
      components: {
          VueBootstrap4Table,
          bInfo,
          bEps,
      },
      name: 'App',
      data: () => {
        return {
          // === === === Sub Module 傳給子元件 Start === === ===
          teamList: 'aTeamList',
          // === === === Sub Module 傳給子元件 End === === ===

          // === === === 按鈕開關 Start === === ===
          show_actionBtn: 0,
          basicGroups: 0,
          // === === === 按鈕開關 End === === ===

          optionalStockText: "自選股",

          gInit: 0,
          gPageCount: 0,
          gRowCount: 0,
          PAGE_COUNT: 2,
          rows: [],
          columns: [],
          customFilters: [],
          total_rows: 0,
          showLoader: true,
          optionalStock: [],



          config: {
            card_title: "Vue Bootsrap 4 advanced 表格",
            // checkbox_rows: true,    //取消選取列
            // rows_selectable: true,  //取消選取列(可以多選)
            multi_column_sort: false,
            highlight_row_hover: true,
            highlight_row_hover_color: "#f8e9ac",
            pagination: true,
            pagination_info: true,
            num_of_visibile_pagination_buttons: 7,
            per_page: 10,
            per_page_options: [10, 20, 30, 50, 3000],
            // per_page_desc:"Ir para pagina",
            page: 1,
            global_search: {
                placeholder: "請輸入搜尋文字",
                visibility: true,
                case_sensitive: false, // default false
                showClearButton: true,
                class: "test",
                // searchOnPressEnter: true,
                searchDebounceRate: 1000,
                // init: {
                //     value: "Christine"
                // }
            },
            show_reset_button: true,
            show_refresh_button: true,
            server_mode: false,
            card_mode: true,
            selected_rows_info: true,
            preservePageOnDataChange: true,
            loaderText: '更新中...',
          },
          classes: {
            // tableWrapper: "",
            table : {
                "table-striped my-class" : true,
                "table-bordered my-class-two" : (rows) => {  // 表格格線
                    return true;
                },
            },
            row : {
                "my-row my-row2" : true,
                "bg-warning" : (row) => { // 漲停
                    return row.b_info.percentage > 9.0;
                },
                "bg-success" : (row) => { // 跌停
                    return row.b_info.percentage <= -9.0;
                },
            },
            cell : {
                "my-cell my-cell2" : true,
                // === === === Column Color Start 顏色定義 === === ===
                "text-danger" : (row, column, cellValue) => {
                    if (column.name === "b_info.price" && row.b_info.change > 0) {
                      return true;
                    } else if (column.name === "b_info.change" && row.b_info.change > 0) {
                      return true;
                    } else if (column.name === "b_info.percentage" && row.b_info.change > 0) {
                      return true;
                    // ----------------------------------------------------------
                    } else if (column.name === "b_eps.price" && row.b_info.change > 0) {
                      return true;
                    } else if (column.name === "b_eps.change" && row.b_info.change > 0) {
                      return true;
                    } else if (column.name === "b_eps.percentage" && row.b_info.change > 0) {
                      return true;
                    }
                },
                "text-success" : (row, column, cellValue) => {
                    if (column.name === "b_info.price" && row.b_info.change < 0) {
                      return true;
                    } else if (column.name === "b_info.change" && row.b_info.change < 0) {
                      return true;
                    } else if (column.name === "b_info.percentage" && row.b_info.change < 0) {
                      return true;
                    // ----------------------------------------------------------
                    } else if (column.name === "b_eps.price" && row.b_info.change < 0) {
                      return true;
                    } else if (column.name === "b_eps.change" && row.b_info.change < 0) {
                      return true;
                    } else if (column.name === "b_eps.percentage" && row.b_info.change < 0) {
                      return true;
                    }
                },
                // === === === Column Color End 顏色定義 === === ===
            },
            // table : "table-striped table-bordered my-class",
          },
          locale: {
            direction: 'ltr', // direction of text
            format: 'DD-MM-YYYY', // fomart of the dates displayed
            separator: ' - ', // separator between the two ranges
            applyLabel: 'Apply',
            cancelLabel: 'Cancel',
            weekLabel: 'W',
            customRangeLabel: 'Custom Range',
          },

        };
      },
      methods: {
          fetchData() {
            let i;

            // this.columns
            axios.get('json/basic_info_cols.json')
              .then( (response) => {
                console.log("basic_info_cols", this.gRowCount);
                // === === load rows === ===
                console.log("basic_info_cols this.gPageCount", this.gPageCount, "this.PAGE_COUNT=", this.PAGE_COUNT);
                if (this.gPageCount === this.PAGE_COUNT) {
                  for (i = 0; i < this.gRowCount; i++) {
                  // for (i = 0; i < 18; i++) {
                    this.rows.push(this['row' + i]);
                    console.log("basic_info_cols", this.rows.push);
                  }
                }

                // ------------------------------
                this.columns = response.data;
                // this.loadOptionalStock();
              })
              .catch( (error) => { // 请求失败处理
                console.log(error);
              });

            this.showLoader = false;
          },
          onSelectRows(payload) {
              console.log(payload);
          },
          // === === === 按鈕開關 Start === === ===
          toggleOptionalStock(index) {
            if (this.basicGroups) {
              // 自選股
              this.loadOptionalStock();
              this.optionalStockText = "自選股" + (index + 1);
            }

            this.basicGroups = !this.basicGroups;
          },
          test() {
              alert(33);
              this.show_actionBtn = false;
          },
          // === === === 按鈕開關 End === === ===

          onRefreshData() {
              console.log('自選股');
              this.toggleOptionalStock(0);
          },


          // === === === Sub Module 由子元件傳回 Start === === ===
          __FetchDone_add() {
            this.gPageCount = this.gPageCount + 1;
            console.log("this.gPageCount", this.gPageCount);
          },
          __FetchDone_check(msg) {
            let i;
            // === === load rows === ===
            console.log("check this.gPageCount", this.gPageCount, "this.PAGE_COUNT=", this.PAGE_COUNT);
            if (this.gPageCount === this.PAGE_COUNT) {
              console.log("msg.length = ", msg.length);
              for (i = 0; i < msg.length; i++) {
                this.rows.push(this['row' + i]);
                // console.log("1111111", this.rows.push)
              }
            }
          },
          _getRows(tableName, msg) {
            let i;

            console.log("[", tableName, "] _getRows");
            for (i = 0; i < msg.length; i++) {
              console.log("this.gInit =", this.gInit);
              if (this.gInit === 0) {
                this['row' + i] = {};
              }

              // console.log("msg.length=", msg[i]["b_eps"]);
              this['row' + i][tableName] = msg[i][tableName];
            }

            this.gInit = 1;
            this.__FetchDone_add();
            this.__FetchDone_check(msg);
          },
          // --------------------------------------------------
          subLoadBInfo(msg) {
            this._getRows("b_info", msg);
          },
          subSelect(msg) {
            this.columns = msg;
          },
          // --------------------------------------------------
          subLoadBEps(msg) {
            this._getRows("b_eps", msg);
          },
          // === === === Sub Module 由子元件傳回 End === === ===

          // === === === 自選股 Start === === ===
          loadOptionalStock() {
            const indexs = [];
            let i = 0;
            this.optionalStock = [
            {
              name : "0050",
              value : "0050",
            },
            {
              name : "00506",
              value : "0056",
            },
            ];

            this.optionalStock.forEach( (element) => {
              indexs.push(i);
              i = i + 1;
            });
            console.log(this.optionalStock);
            this.columns[0].filter.options = this.optionalStock;
            this.columns[0].filter.init.value = indexs;
          },
          // === === === 自選股 End === === ===
      },

      created() {
        this.gInit = 0;
        this.show_actionBtn = true;

        this.customFilters.push({
            name: "b_info.code",
            text: "0050",
            type: "custom",
        });
        this.customFilters[0].text = "0050";
      },
      mounted() {
          this.fetchData();

          // let test = [1,2,3,4,5,6,7]

          // test.some((val,index) => {
          //     console.log(val);
          //     if (val == 3) {
          //         return true
          //     }

          // })
      },
    };
</script>


<style scoped src="./assets/css/freeze_table.css"/>
<style scoped src="./assets/css/freeze_table_phone.css"/>
<style scoped src="./assets/css/color.css"/>

