<template slot="vbt-action-buttons">
    <div class="btn-group float-left" role="group" aria-label="Basic example">
      <button type="button" class="btn btn-outline-primary" @click="selectColumns">
        <span> <i class="fas fa-download"></i> 獲利能力_歷年加權平均</span>
      </button>
    </div>
</template>

<script>
  import axios from 'axios';

  export default {
      name: 'bEps',
      my_rows: {},
      my_cols_done: 0,
      my_cols: {},
      props: ['teamList'],
      methods: {
        fetchData() {
          axios.get('json/basic_eps.json')
            .then( (response) => {
              this.my_rows = response.data;
              console.log("get json/basic_eps.json");
              // console.log(response.data);

              this.$emit('subLoadRows', this.my_rows);
            })
            .catch( (error) => { // 请求失败处理
              console.log(error);
            });
        },

        selectColumns() {
          if (this.my_cols_done === 0) {
            axios.get('json/basic_eps_cols.json')
              .then( (response) => {
                this.my_cols = response.data;
                console.log("get json/basic_eps_cols.json");
                // console.log(response.data);
                this.my_cols_done = 1;

                this.$emit('subSelectColumns', this.my_cols);
              })
              .catch( (error) => { // 请求失败处理
                console.log(error);
              });
          } else {
            console.log("without get json/basic_eps_cols.json");
            this.$emit('subSelectColumns', this.my_cols);
          }
        },
      },
      mounted() {
          this.my_cols_done = 0;
          this.fetchData();

          // this.$emit('transferColumns', this.my_rows);
      },
  };
</script>